<template>
	<div class="goods">
		<headerNav title="条码搜索"/>

		<i-input ref="searchBar" v-model="serialNumber" size="large" class="search" placeholder="请输入商品条码" @on-enter="search">
			<Button slot="append" icon="ios-search" @click="search"></Button>
			<!--<Button id="scanButton" disabled slot="prepend" icon="ios-qr-scanner" @click="scanBarCode"></Button>-->
		</i-input>

		<!--<div id="bcid"></div>-->

		<div v-if="notFound" class="product-not-found-container">
			<div class="product-not-found-img">
				<img src="../../../static/img/goodsList/notfound.png" width="30%" height="30%">
			</div>
			<div class="product-not-found-txt">未找到相关商品</div>
		</div>

		<van-goods-action>
			<van-goods-action-big-btn primary @click="toCart">
				查看购物车
			</van-goods-action-big-btn>
		</van-goods-action>

		<van-sku
				v-model="isSkuShow"
				:sku="skuData.sku"
				:goods="skuData.goodsInfo"
				:goods-id="skuData.goodsInfo.id"
				:hide-stock="true"
				:hide-quota-text="true"
				:quota="0"
				:close-on-click-overlay="true"
				@add-cart="addToCart"
		>
			<template slot="sku-actions" slot-scope="props">
				<div class="van-sku-actions">
					<van-button type="primary" bottom-action @click="props.skuEventBus.$emit('sku:addCart')">加入购物车</van-button>
				</div>
			</template>

			<template slot="sku-header-price" slot-scope="props">
				<div style="display: flex; flex-direction: column;">
					<div style="color: red;">￥{{goodsInfo.SpecialOffer}}</div>
					<div>
						<span v-if="goodsInfo.Quota !== -1" style="margin-right: 10px; font-size: 12px;">限购：{{goodsInfo.Quota}}件</span>
						<span style="font-size: 12px;">库存：{{goodsInfo.Stock}}件</span>
					</div>
				</div>
			</template>

			<template slot="sku-group" slot-scope="props">
				<div class="van-sku-group-container">
					<div class="van-sku-row" v-for="(items, index) in goodsInfo.ValuesList" :key="index">
						<div class="item-select-title">
							<p>{{specs[goodsInfo.ValuesList[index][0].CategoryType - 1]}}：</p>
						</div>
						<div class="item-select-column">
							<div class="item-select-row">
								<RadioGroup v-model="selectString[index]" type="button" @on-change="select(index)">
									<Radio v-for="(item, index1) in items" :key="index1" :label="item.GoodsValueName"></Radio>
								</RadioGroup>
							</div>
						</div>
					</div>
				</div>
			</template>

			<template slot="sku-stepper" slot-scope="props">
				<div class="van-sku-stepper-stock">
					<div class="van-sku-stepper-container">
						<div class="van-sku__stepper-title">购买数量：</div>
						<div class="van-stepper van-sku__stepper">
							<van-stepper v-model="count" integer :min="1" :max="maxCount" input-width="50px"/>
						</div>
					</div>
				</div>
			</template>
		</van-sku>
	</div>
</template>

<script>
	import headerNav from '../../components/header/nav'
	import {mapState, mapActions} from 'vuex';

	export default {
		name: "Serial",

		components: {
			headerNav
		},

		data() {
			return {
				serialNumber: '',
				maxCount: 10000,
				notFound: false,
				isSkuShow: false,
				skuData: {
					sku: {
						tree: [],
						list: [],
						price: '',
					},
					goodsInfo: {
						id: null,
						title: '',
						picture: '',
					}
				},
				selectString: [],
				selectIndex: [],
				specs: ['味道', '颜色', '尺寸'],
				count: 1,
			};
		},

		computed: {
			...mapState(['goodsInfo', 'searchResult']),
		},

		mounted() {
			this.$refs['searchBar'].focus();

			// document.addEventListener("plusready", () => {
			// 	let scanButton = document.getElementById("scanButton");
			// 	scanButton.removeAttribute( "disabled" );
			//
			// 	this.scan = new plus.barcode.Barcode('bcid');
			// 	this.scan.onmarked = async (type, result) => {
			// 		alert(result);
			// 		this.serialNumber = result;
			// 		this.scan.close();
			// 		await this.search();
			// 	};
			// });
		},

		methods: {
			...mapActions(['loadGoodsInfo', 'loadSerialSearchResult']),

			// scanBarCode() {
			// 	this.scan.setFlash(true);
			// 	this.scan.start();
			// },

			async search() {
				if (this.serialNumber.trim() === '') {
					this.$Message.error("请输入商品条码");
					return;
				}

				await this.loadSerialSearchResult(this.serialNumber);
				if (this.searchResult.GoodsModel.length === 0) {
					this.notFound = true;
				} else {
					this.notFound = false;
					await this.loadGoodsInfo(this.searchResult.GoodsModel[0].GoodsID);
					this.skuData.sku.price = this.goodsInfo.SpecialOffer;
					this.skuData.goodsInfo.id = this.goodsInfo.GoodsID;
					this.skuData.goodsInfo.title = this.goodsInfo.GoodsName;
					this.skuData.goodsInfo.picture = this.goodsInfo.Image;
					this.initSelectString();
					this.count = 1;
					this.isSkuShow = true;
					this.maxCount = this.goodsInfo.Quota > 0 ? this.goodsInfo.Quota : this.maxCount;
					this.maxCount = Math.min(this.maxCount, this.goodsInfo.Stock);
				}
			},

			initSelectString() {
				this.selectString = [];
				for (let i = 0; i < this.goodsInfo.ValuesList.length; i++) {
					this.selectString.push(this.goodsInfo.ValuesList[i][0].GoodsValueName);
					this.selectIndex[i] = this.goodsInfo.ValuesList[i][0].GoodsValueID;
				}
			},

			select(index) {
				this.goodsInfo.ValuesList[index].forEach(item => {
					if (item.GoodsValueName === this.selectString[index]) {
						this.selectIndex[index] = item.GoodsValueID;
					}
				});
			},

			toCart() {
				this.$router.push('/cart');
			},

			async addToCart() {
				let specs = [];
				for (let i = 0; i < this.selectIndex.length; i++) {
					let tmp = {
						'CategoryType': i + 1,
						'GoodsValueID': this.selectIndex[i],
					};
					specs.push(tmp);
				}
				let res = await this.$request.addCart(this.goodsInfo.goodsID, this.count, specs);
				if (res) {
					this.$Message.success('加入购物车成功');
				}
				this.isSkuShow = false;
				this.serialNumber = '';
				this.$refs['searchBar'].focus();
			},
		}
	};
</script>

<style lang="less" scoped>
	.goods {
		padding-bottom: 50px;
		&-tag {
			font-size: 12px;
			border-top: 1px solid #e5e5e5;
			span {
				margin-right: 10px;
			}
			i {
				color: red;
				margin-right: 3px;
			}
			img {
				width: 12px;
				margin-right: 3px;
				margin-top: 6px;
			}
		}
		&-title {
			line-height: 18px;
			padding-top: 10px;
			margin-bottom: 6px;
			font-size: 14px;
			color: #333;
			font-weight: 700;
			border-top: 1px solid #f0f0f0;
		}
		&-subtit {
			font-size: 13px;
			color: #333;
			line-height: 21px;
		}
		&-price {
			color: #f44;
			font-size: 20px;
		}
		&-market-price {
			text-decoration: line-through;
			margin-left: 8px;
			font-size: 13px;
			color: #999;
		}
		&-cell-group {
			margin: 15px 0;
			.van-cell__value {
				color: #999;
			}
		}
		&-info-title {
			height: 44px;
			line-height: 44px;
			text-align: center;
			font-size: 14px;
			font-weight: 700;
			margin: 10px;
			border-top: 1px solid #e5e5e5;
		}
		&-info p {
			margin: 0;
			padding: 0;
			margin-block-end: 0;
			margin-block-start: 0;
			display: grid;
		}
		&-info img {
			width: 100%;
		}
	}

	.item-select-title {
		color: #000000;
		font-size: 14px;
		margin-right: 15px;
	}

	.item-select-column {
		margin-top: 5px;
		display: flex;
		flex-direction: column;
	}

	.item-select-row {
		display: flex;
		flex-direction: row;
		margin-bottom: 8px;
	}

	.product-not-found-container {
		margin-top: 40%;
		width: 100%;
		height: 100%;
	}

	.product-not-found-img {
		margin-top: 15px;
		margin-bottom: 15px;
		text-align: center;
	}

	.product-not-found-txt {
		text-align: center;
		font-size: 14px;
		color: #8c8c8c;
	}
</style>


